.card {
	position: relative;
	width: 306px;
	height: 434px;
	margin-left: 32px;

	
	cursor: pointer;
	
	overflow: hidden;
	box-shadow: 1px 1px 4px 0 rgba(0,0,0,0.05), 2px 4px 32px 0 rgba(0,0,0,0.1), 0 0 64px 0 rgba(0,0,0,0.2);
	
	font-family: 'Open Sans', Helvetica, sans-serif;
	font-weight: 300;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	
	transition: transform 300ms ease;
}	
	.image {
		position: absolute;
		top: 0; right: 0; bottom: 70px; left: 0;
		background-position: center;
		background-size: cover;
        transition: bottom 300ms ease;
	}
    
    .image1 {
        background-image: url(/matthew-courtney/static/media/equippe.02e10533.png)
    }

    .image2 {
        background-image: url(/matthew-courtney/static/media/employee.0cdca106.png)
    }

    .image3 {
        background-image: url(/matthew-courtney/static/media/pokedex.010ba81f.png)
    }



	.content {
		position: absolute;
		z-index: 10;
		height: 100%;
		background: white;
		transform: translateY(364px);
		transition: transform 300ms ease;
	}	
	.content .header {
			font-size: 1.5rem;
            font-weight: 700;
            padding-top: 7px;
            padding-bottom: 0;
		}
		
	.content .meta {
			font-size: 0.8rem;
			color: #888;
			margin-bottom: 0.5rem;
		}
		
	.content .description {
			opacity: 0;
			transition: opacity 500ms ease 100ms;
		}
	
	
	.content:hover {
		transform: translateY(-16px);
		
		
		
		
	}
.content:hover .image {
			bottom: 280px;
        }

        .content:hover .header {
			font-size: 1.5rem;
            font-weight: 700;
            padding-top: 20px;
        }      
        
        .content:hover .description {
			opacity: 100;
			transition: opacity 500ms ease 100ms;
        }
        



        /* Responsive Sectiom */

        @media (max-width: 1400px) {
            .portfolio{
                margin-left: 0;
                margin-right: 0;
                background-color: gray;
                max-width: 100vw;
            }
           .container-custom {
               padding: 0;
               background-color: white;
           }
           .row {
               max-width: 100%;
               
           }
           .col-lg-3 {
            padding-left: 20px;
            margin-right: auto;
            margin-left: auto;
            padding-right:0;
           }
           .card {
            margin-right: auto;
            margin-left: auto; 
            margin-bottom: 30px;
            padding:0;
           }
            }


            @media (min-width: 2000px) {
                .heading {
                    padding-top: 25px;
                    padding-bottom: 30px;
                    font-family: Roboto,Helvetica,sans-serif;
                }
                .portfolio{
                    background-color:  rgba(44, 146, 241, 0.8);
                    text-align: center;
                    max-width: 1550px;
                    margin-left: 35%;
                    margin-right: auto;
                    padding-bottom: 40px;
                }
                .col-lg-3 {
                    margin-left: auto;
                    margin-right: auto;
                    padding-bottom: 30px;
                }
                .card3 {
                    padding-right:35px;
                }
                .card {
                    width: 356px;
                    height: 484px;
                }
                .content {
                    transform: translateY(414px);
                    transition: transform 300ms ease;
                }
                }
